import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'
import GlobalSettings from '@/GlobalSettings'
import store from '../store'

const entity = {
  id: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
    form: false,
  },
  sort: {
    displayName: 'sectionSort',
    type: IKDataEntity.Types.Integer,
  },
  image: {
    type: IKDataEntity.Types.Image,
    formConfig: {
      type: {
        root: () => store.getters.rootUrl + GlobalSettings.dishImageRoot,
      },
    },
    displayName: 'image',
  },
}
const config = {
  add: function (item) {
    return hillo.postWithUploadFile('Restaurant.php?op=uploadImage', {
      ...item,
    }, { showLoading: true })
  },
  edit: function (item) {
    if (item.file) {
      item.imageExt = item.file.name.split('.')[1]
    }
    return hillo.postWithUploadFile('Restaurant.php?op=modifyRestaurantImage', {
      ...item,
      params: JSON.stringify(item),
    }, { showLoading: true })
  },
  remove: function (id) {
    return hillo.post('Restaurant.php?op=deleteRestaurantImage', { id: id })
  },
  load: async function (filter) {
    return (await hillo.get('Restaurant.php', {
      op: 'showAllRestaurantImage',
      ...filter,
    }))
      .content
  },
}

export default IKDataEntity.ModelFactory(entity, config)
